.links-container {
    max-width: 1120px;
    margin: 32px auto 0 auto;
}

.links-header {
    display: flex;
    flex-direction: row;
}

.links-header svg {
    margin-right: 14px;
}

.links-header h1 {
    color: #FFF;
    font-size: 24px;
    margin-bottom: 18px;
}

.item {
    width: 700px;
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-top: 12px;
}

.item:last-child {
    margin-bottom: 18px;
}

.link {
    border: 0;
    width: 100%;
    min-width: 80%;
    border-radius: 12px;
    height: 46px;
    color: #FFF;
    background-color: rgb(255, 255, 255, 0.15);
    margin-right: 14px;
    display: flex;
    align-items: center;
    padding: 0 14px;
    font-size: 18px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.link svg {
    margin-right: 14px;
}

.trash {
    border: 0;
    background: transparent;
}

.empty-text {
    width: 100%;
    text-align: center;
    color: #FFF;
    margin-top: 28px;
}

@media (max-width: 730px) {
    .links-container {
        width: 90%;
    }
    .logo h1 {
        font-size: 18px;
    }

    .item {
        width: 100%;
    }

    .link {
        width: 100%;
    }

    @media (max-width: 415px) {
        .link svg {
        display: none;
    }
    }
}