.container-home {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.logo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo h1 {
    font-size: 60px;
    color: white;
}

.logo span {
    color: white;
    margin-top: 14px;
    margin-bottom: 34px;
    font-size: 19px;
}

.area-input {
    width: 540px;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: slideY 2s;
}

@keyframes slideY{
    from{
        transform: translateY(90px);
    } to {
        transform: translateY(0px);
    }
}

.area-input div {
    width: 100%;
    height: 46px;
    background-color: rgb(255, 255, 255, 0.15);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
}

.area-input svg {
    margin: 0 8px;
}

.area-input input {
    width: 100%;
    background-color: transparent;
    border: 0;
    height: 46px;
    color: #FFF;
    outline: none;
    font-size: 18px;
}

.area-input input::placeholder {
    color: rgb(255, 255, 255, 0.8);
}

.area-input button {
    width: 100%;
    height: 46px;
    border-radius: 12px;
    border: 0;
    font-size: 16px;
    color: #000;
    font-weight: bold;
    margin-top: 12px;
    transition: transform 0.7s;
}

.area-input button:hover {
    transform: scale(1.05);
}

@media (max-width: 610px){
    .logo img {
        width: 50%;
    }
    .logo h1 {
        font-size: 32px;
    }
    
    .logo span {
        color: white;
        margin-top: 14px;
        margin-bottom: 34px;
        font-size: 19px;
    }
    
    .area-input {
        width: 340px;
    }
    
    .area-input div {
        width: 100%;
        height: 46px;
    }
    
    .area-input input {
        width: 100%;
        font-size: 14px;
    }
    .area-input button {
        width: 60%;
        height: 46px;
        border-radius: 50px;
        font-size: 16px;
    }
    
}